    .row.bg-3
      .col-12
        .p-5 
          .row.justify-content-center.align-items-center
            .col-lg-4
              p La contabilidad refleja la realidad econmica y financiera de la empresa, en la que se tiene en cuenta el ciclo contable para obtener como producto los estados financieros de propsito general. En cumplimiento de los estndares internacionales se aplican los criterios de reconocimiento, medicin, presentacin y revelacin de los estados financieros, estipulados en las polticas contables diseadas por la entidad, de acuerdo con la Norma Internacional de Informacin Financiera (NIIF) y los grupos de presentacin de informacin financiera.
            .col-lg-4.d-lg-block.d-none
              figure
                img(src='@/assets/curso/tema1/img1.png', alt='', style="width: 400px").m-auto
            .col-lg-4 
              p.p-4.bg-4(data-aos="fade-right") Cabe resaltar que los estados financieros que se generan requieren de la aplicacin de tcnicas de anlisis e indicadores financieros; cuyo objetivo es interpretar los valores monetarios para la elaboracin de informes comprensibles y tiles para la toma de decisiones de liquidez, solvencia y rentabilidad.

          .row.justify-content-center.align-items-center
            .col-lg-5.d-lg-block.d-none
              figure
                img(src='@/assets/curso/tema1/img2.png', alt='', style="width: 505px").m-auto
            .col-lg-7
              p El anlisis financiero es una herramienta de apoyo a la gerencia porque establece los resultados financieros para identificar las debilidades y fortalezas, con el fin de disear e implementar estrategias para dar valor agregado a la empresa. Adicionalmente, es til para las entidades financieras y acreedores, teniendo en cuenta que determinan la capacidad de endeudamiento y liquidez de la compaa. Finalmente, permite a los socios y propietarios conocer los niveles de rentabilidad de la inversin realizada.

              p.p-3.bg-5(data-aos="fade-right") A partir de los cambios normativos en el mbito internacional y con respecto a la norma local se establecen criterios y bases de medicin diferentes que de cierta manera afectan los resultados finales. Es de resaltar que se manejan cambios en el reconocimiento y medicin de las cuentas de activos diferidos, obligaciones financieras, deudores, cuentas por pagar y estimaciones contables como es el caso de la vida til de propiedades, planta y equipo.
          
          p Para realizar adecuadamente el anlisis financiero es importante que la informacin financiera tenga en cuenta las cualidades fundamentales y de mejora que se relacionan a continuacin:

          .row.justify-content-center
            .col-sm-6.col-lg-3.mb-4.mb-lg-0
