            h2(data-aos="fade-right") 1.2 Metodologa de clculo de las tcnicas de anlisis horizontal y vertical

          p Para aplicar la tcnica de anlisis vertical se requiere un estado financiero, teniendo en cuenta que se identifica la proporcin o relacin que tiene el total del elemento, con respecto a cada una de sus partes. 

