          p.mt-4 En el caso del estado de situacin financiera se tiene en cuenta un perodo y se analiza cada uno de sus elementos, por ejemplo, en el activo se toma como base el total del activo (100 %), posteriormente se identifica el porcentaje que representa cada una de las cuentas que se encuentran relacionadas en este concepto, para ello, se aplica la siguiente frmula: 

          .row.justify-content-center.align-items-center
