    p.mt-4 Para desarrollar esta tcnica se requiere mnimo dos estados financieros homogneos de perodos consecutivos, cuyo objetivo es realizar el comparativo y determinar los aumentos o disminuciones generados de un perodo al otro, con el fin de tomar decisiones oportunamente. Cabe resaltar que esta tcnica se puede aplicar a dos o ms perodos, con el fin de analizar la trazabilidad de la empresa y evaluar el desempeo de esta.

    p El procedimiento que se desarrolla para aplicar dicha tcnica consiste en determinar la variacin absoluta y variacin relativa, con el fin de analizar los aumentos o disminuciones generados en los perodos analizados:

    .row
      .col-lg-8.order-2.order-lg-1
        TabsA.color-acento-botones.mb-5
          .tarjeta.clr-paleYellow.p-4(titulo="Variacin absoluta")
            h4 Variacin absoluta
            p Para hallar la variacin absoluta se toman los datos de dos perodos, restando el ao actual y el ao anterior, el cual se representa en valores monetarios. La frmula aplicable es la siguiente:

            figure.d-block.mx-auto
              img(src="@/assets/curso/tema1/img34.svg", alt="Frmula de variacin absoluta: resta del valor del ao actual menos el valor del ao anterior.", style="width:352px").m-auto

          .tarjeta.clr-paleYellow.p-4(titulo="Variacin relativa")
            h4 Variacin relativa
            p En el caso de la variacin relativa se tiene en cuenta la variacin absoluta (ao actual - ao anterior), dividido en el ao anterior, cabe resaltar que se multiplica por 100 para obtener el porcentaje.
            figure.d-block.mx-auto
              img(src="@/assets/curso/tema1/img35.svg", alt="Frmula de variacin relativa: resta del valor del ao actual menos el valor del ao anterior, dividido entre el valor del ao anterior y multiplicado por 100.", style="width:462px").m-auto

